  letter-spacing:.5px;
  line-height:40px;
  margin-right:1rem;
  font-weight:400
}

.nav-item:after{
  content:"";
  display:block;
  height:3px;
  width:0;
  background:transparent;
  letter-spacing:.5px;
  transition:width .7s ease,background-color .5s ease
